.StuffList {
  display: flex;
  flex: 8;
  flex-direction: column;
  &__Title {
    margin-bottom: 45px;
    color: $White;
  }
  &__Stuffs {
    display: grid;
    grid-template-columns: repeat(auto-fill, 350px);
    gap: 30px;
    width: 100%;

    a {
      text-decoration: none;
    }
  }
}
